@import "../../styles/index.scss";

$subtitle-size: 1vw;

.HeaderContainer{
    background-color: transparent;
    @include PaddingTopBottom(5vh !important);
    transition: all $transition-time ease-out;
}

.HeaderContainerShrunk{
    background-color: $primary-color;
    @include PaddingTopBottom(0vh !important);
    transition: all $transition-time ease-out;
}

%NavBrand {
    text-decoration: none !important;
    color: $accent-color;
    font-family: $brand-font;
}

.NavBrand {
    @extend %NavBrand;
    font-size: 2.6vw;
    transition: font-size $transition-time;
}

.NavBrandShrunk {
    @extend %NavBrand;
    font-size: 2vw;
    transition: font-size $transition-time;
}

.NavBrandContainer{
    &:hover a{
        color: $accent-color !important;
    }
    &:active a {
        color: $accent-color !important;
    }
    &:hover svg{
        transform: rotate(360deg);
        // color: $accent-hover-color !important;
    }
}

.NavLink{
    @include PaddingLeftRight(1vw);
    text-decoration: none !important;
    @include PrimarySubTitle($subtitle-size);
    &:hover{
        color: $primary-text-hover-color;
    }
    &:active{
        color: $primary-text-color;
    }
    &:focus{
        color: $primary-text-color;
    }
}

.NavContainer:hover{
    opacity: 1;
    background-color: transparent;
    border-width: 10px;
    border-radius: 5px;
}

.SignInButton{
    @extend .PrimaryOutlineButton;
    @include PrimarySubTitle($subtitle-size);
    &:hover{
        color: $secondary-text-color !important;
    }
}

.UBCLogo{
    -webkit-filter: brightness(0) invert(1); /* Safari 6.0 - 9.0 */
    filter: brightness(0) invert(1);
}

.HeaderLogo{
    max-height: 10vh;
    min-width: 10vh;
    color: $accent-color;
    transition: transform 1s ease;
}

